@import url(https://fonts.googleapis.com/css2?family=Londrina+Solid:wght@300&display=swap%27);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0;
  padding: 0;
  background-color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: "Londrina Solid";
  color: #fdfac2;
  font-size: 1.2rem;
  text-align: justify;
  text-justify: inter-word;
}
#openimage {
  display: flex;
  justify-content: center;
  align-items: center;
}
#openimage img {
  max-width: 60vh;
  max-height: 60vh;
}
#openbuttons {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
#openbuttons img {
  max-width: 35vh;
  max-height: 35vh;
  border-radius: 10px;
  margin: 20px;
}
#desc {
  max-width: 80vh;
  margin: 20px;
}

:root {
	--pagination-color: #a5dd1a;
}

.pagination {
	margin: 15px auto;
	display: flex;
	list-style: none;
	outline: none;
}

ul.pagination {
	display: flex;
	justify-content: center;
	align-items: center;
	-webkit-padding-start: 0px;
	        padding-inline-start: 0px;
}

.pagination > .active > a {
	background-color: #a5dd1a;
	background-color: var(--pagination-color);
	border-color: #a5dd1a;
	border-color: var(--pagination-color);
	color: #fff;
}
.pagination > li > a {
	border: 1px solid #a5dd1a;
	border: 1px solid var(--pagination-color);
	padding: 5px 10px;
	outline: none;
	cursor: pointer;
}
.pagination > .active > a,
.pagination > .active > span,
.pagination > .active > a:hover,
.pagination > .active > span:hover,
.pagination > .active > a:focus,
.pagination > .active > span:focus {
	background-color: #a5dd1a;
	background-color: var(--pagination-color);
	border-color: #a5dd1a;
	border-color: var(--pagination-color);
	outline: none;
}
.pagination > li > a,
.pagination > li > span {
	color: #a5dd1a;
	color: var(--pagination-color);
}
.pagination > li:first-child > a,
.pagination > li:first-child > span,
.pagination > li:last-child > a,
.pagination > li:last-child > span {
	border-radius: unset;
}

.listCard {
	background-image: url(/CC-React_morty/static/media/backgroundPic.6950e761.png);
	background-position: center;
	background-size: 100%;
	border: solid 2px palegoldenrod;
	border-radius: 8px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin: 10px;
	padding: 10px;
	
}


.location {
	cursor: pointer;
}

.listCard img {
	cursor: pointer;
}

.listCard p {
	text-shadow: 1px 1px 5px black;
	font-size: 1.8rem;
}

.listCard h1 {
	text-shadow: 1px 1px 5px black;
}

.modal {
	padding: 20px;
	display: flex;
	justify-content: center;
	align-items: center;
}
.popUpCard {
	color: #406606;
	background-color: whitesmoke;
	opacity: 0.85;
	padding: 20px;
	border: solid 10px #68a50a;
	border-radius: 30px;
	width: 60%;
	font-size: 1.5rem;
	font-weight: bold;
	outline: none;
}
.popUpData {
	color: rgb(196, 131, 10);
}

.listCard img {
	border-radius: 7px;
	box-shadow: 1px 1px 5px black;
}

